@mixin flex($justifyContent, $flexDirection, $alignItems) {
  display: flex;
  justify-content: $justifyContent;
  flex-direction: $flexDirection;
  align-items: $alignItems; }

@mixin homeWeb( $height, $padding) {
  padding: $padding;
  margin: 0;
  background: no-repeat url("../img/fong.png");
  // background-position: top
  background-size: 100%;
  // width: $width
  height: $height; }

@mixin homeMobile( $height, $padding) {
  padding: $padding;
  margin: 0;
  background: no-repeat url("../img/bigFong.png");
  // background-position: top
  background-size: 100%;
  // width: $width
  height: $height; }

.imgPanelHomeWeb {
  @include homeWeb( 100%, (0px 0px 0px 0px)); }

.imgPanelHomeMobile {
  @include homeMobile( 100%, (0px 0px 20px 0px)); }

.ResultPanelMobile {
  @include homeMobile( 800px, (0px 0px 20px 0px)); }

.ResultPanelWeb {
  @include homeWeb( 100%, (0px 0px 20px 0px)); }

