.container {
  background-color: #2a7fffd1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding: 80px 20px 20px 20px;

  &__icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;

    svg {
      width: 80px;
      height: 80px;
      color: white;
    }
  }

  &__main {
    width: 80%;

    .header {
      text-align: center;
      width: 100%;
      font-size: 30px;
      color: white;
    }

    .title {
      margin-top: 20px;
      text-align: center;
      width: 100%;
      font-size: 15px;
      color: white;
    }

    .description {
      margin-top: 20px;
      text-align: center;
      width: 100%;
      font-size: 15px;
      color: #0000009e;
    }
  }

  .buttons {
    height: 50px;
    width: 100%;

    button {
      height: 50px;
      background-color: white;
      margin-top: 5px;
      border-radius: 40px;

      span {
        color: black;
        font-size: 20px;
      }
    }
  }
}
